import React, { useState, useContext } from "react";
import { useNavigate } from "react-router-dom";
import { UserContext } from "../context/userContext";

const SignUp = () => {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [confirmpassword, setConfirmPassword] = useState("");
  const { setUser } = useContext(UserContext)
  const navigate = useNavigate();
  
   async function registerUser() {
    try {
      const result = await fetch("http://localhost:3001/users/signup", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          name,
          username,
          password,
          email,
        }),
      });
      const json = await result.json();
      const verifiedUser = {user: json.user.username, token: json.token, userId: json.user.id}
      localStorage.setItem('showcase-token', JSON.stringify(verifiedUser))
      setUser(json.user)
    } catch (err) {
      console.log(err);
    }
  }

  const handleSubmit = async (e) => {
    e.preventDefault()
    registerUser();
    navigate("/");
  };


  return (
    <section className="section bg-black bg-no-repeat bg-cover">
      <div className="min-h-screen py-40">
        <div className="container mx-auto ">
          <div className="flex flex-col lg:flex-row w-8/12 bg-gradient-to-r from-red-400 via-red-500 to-red-600 rounded-xl mx-auto shadow-lg overflow-hidden">
            <div className="w-full lg:1/2 flex flex-col p-12 py-16 px-12">
              <h2 className="text-white text-3xl mb-4 font-fourth animate-bounce"> Sign Up </h2>
              <p className='font-primary text-2xl mb-4'>Join the family</p>



              <form action="#">
                <div className="mt-5">
                  <input value={name} onChange={(e) => setName(e.target.value)} type="name" required
                    placeholder="Name"
                    className="text-black border border-gray-400 py-1 px-2 w-full rounded-xl mx-auto"
                  />
                </div>
                <div className="mt-5">
                  <input value={email} onChange={(e) => setEmail(e.target.value)} type="email" required
                    placeholder="Email"
                    className=" text-black border border-gray-400 py-1 px-2 w-full rounded-xl mx-auto"
                  />
                </div>
                <div className="mt-5">
                  <input value={username} onChange={(e) => setUsername(e.target.value)} type="Username" required
                    placeholder="Username"
                    className="text-black border border-gray-400 py-1 px-2 w-full rounded-xl mx-auto"
                  />
                </div>
                <div className="mt-5">
                  <input value={password} onChange={(e) => setPassword(e.target.value)} type="password" required
                    placeholder="Password"
                    className="text-black border border-gray-400 py-1 px-2 w-full rounded-xl mx-auto"
                  />
                </div>
                <div className="mt-5">
                  <input value={confirmpassword} onChange={(e) => setConfirmPassword(e.target.value)} type="password" required
                    placeholder="Confirm Password"
                    className="text-black border border-gray-400 py-1 px-2 w-full rounded-xl mx-auto"
                  />
                </div>
                <div className="mt-5">
                  <button onClick={handleSubmit}
                    className="bg-gradient-to-r from-red-400 via-red-500 to-red-600 hover:bg-gradient-to-bl focus:ring-4 focus:outline-none focus:ring-red-300 dark:focus:ring-red-800 active:scale-[.98] active:duration-75 hover:scale-[1.01] ease-in-out transition-all rounded-xl btn mb-[30px] animate-pulse"
                  >
                    <div className="text ">Login</div>
                  </button>
                </div>
              </form>
            </div>
            <div className="w-full lg:1/2 flex flex-col p-12 bg-lens2 bg-no-repeat bg-cover bg-center">
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default SignUp;
